body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  text-align: center
}

div.dynamicUpdateTime {
  text-align: center;
  border: 1px solid #666666;
}
div.father{
  border:1px solid red;
  text-align: center;
  padding:60px;
}
.App {
  color: #333;
  border: 1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.15);
}



.App>.todoList>li:nth-child(odd) {
  background: #eee;
}

.App>.todoList>li:nth-child(even) {
  background: transparent;
}
.TodoInput {
    width: 100%;
    line-height: 1.8;
    margin-bottom: 1em;
    font-size: 16px;
    padding: 0 .2em;
}
.TodoItem {
    padding: .5em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.TodoItem>.title {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 0 .5em;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::after {
    box-sizing: border-box;
}

*::before {
    box-sizing: border-box;
}

ul,
ol,
dl {
    list-style: none;
}

a {
    color: inherit;
}
